export default {
    KEY_API: "39494442-15ddf43d3d3b42079237eb6d3",
    URL_API: "https://pixabay.com/api/videos/",
    async getListVideos({ trend, page = 1 }) {
      const URL = `${this.URL_API}?key=${this.KEY_API}&q=${trend}&page=${page}`;
      const response = await fetch(URL);
      const data = await response.json();
      return data;
    },
    async getDetailsVideoObject(id) {
      const URL = `${this.URL_API}?key=${this.KEY_API}&id=${id}`;
      const response = await fetch(URL);
      const data = await response.json();
      return data;
    },
  };
  