<template>
  <ul class="list-group ">
    <GroupItem v-for="(item, index) in items" :key="index" :item="item" @trend="trend"/>
  </ul>
</template>

<script>
import GroupItem from "./GroupItem.vue";
export default {
  name: "listGroup",
  props: {
    items: Array,
  },
  components: { GroupItem },
  methods: {
    trend(trend) {
      this.$emit('trend',trend)
    }
  }
};
</script>
