<template>
  <div class="form-check mb-1">
    <input
      class="form-check-input me-1"
      type="checkbox"
      v-model="checkbox"
      :id="value"
      @click="sendCheckbox"
    />
    <label class="form-check-label" style="font-weight: 600" :for="value">
      {{ textLabel }}
      <slot></slot>
    </label>
  </div>
</template>

<script>
export default {
  name: "inputCheckbox",
  props: {
    value: String,
    textLabel: String,
  },
  data() {
    return {
        checkbox:false
    };
  },
  methods:{
    sendCheckbox() {
        this.checkbox = !this.checkbox
        this.$emit('checkboxValue',this.checkbox)
    }
  }
};
</script>

<style></style>
