<template>
  <NavbarMain @searchValue="searchValue" />
  <div
    class="home w-100 d-flex"
    :class="{ modeDark: mode }"
    style="margin-top: 75px; transition: 0.5s;min-height: 100vh;"
  >
    <ManualDiv
      @showManual="showManualBoolean"
      class="m"
    >
      <MerchantType />
      <div class="bor mt-3"></div>
      <div class="newInteractions p-1 pt-2">
        <h5 class="pb-2">New Interactions</h5>
        <div class="d-flex mb-3 justify-content-center align-items-center">
          <i class="fa-solid fa-image"></i>
          <div @click="displayType($refs.action)" class="ActionButton mx-2">
            <span
              ref="action"
              class="fa-solid fa-yin-yang"
              style="color: rgb(0, 124, 158)"
            ></span>
          </div>
          <i class="fa-solid fa-film"></i>
        </div>
        <InputChechboxVue
          @checkboxValue="checkboxFavorite"
          :textLabel="'Favorite'"
          :value="'favorite'"
        >
          <i
            class="fa-solid fa-heart-circle-check fa-beat-fade"
            style="color: #ff9e9e"
          ></i>
        </InputChechboxVue>
        <div class="accordion accordion-flush" id="accordionFlushExample">
          <AccordinItem
            :textButton="'Videos'"
            :classIcon="'fa-solid fa-clapperboard'"
          >
            <ListGroup
              v-if="showVideos"
              :items="[
                'Watches',
                'Electrical Tools',
                'Sport shoes',
                'Cars',
                'Touristic places',
                'Accessories',
                'Clothing products',
                'Modern cars',
              ]"
              @trend="updateTrend"
            />
            <div
              v-else
              class="p-2 rounded text-white"
              style="background-color: rgba(255, 0, 0, 0.522)"
            >
              <span
                >Choose video from the options
                <i
                  class="fa-solid fa-up-long fa-bounce fs-3 ms-5 position-absolute"
                  style="top: 300px"
                ></i>
              </span>
            </div>
          </AccordinItem>

          <AccordinItem
            :textButton="'Products'"
            :classIcon="'fa-brands fa-product-hunt fa-shake'"
          >
            <ListGroup
              :items="[
                'Watches',
                'laptop',
                'Sport shoes',
                'phone',
                'Touristic places',
                'jewelry',
                'headphones',
                'glasses',
              ]"
              @trend="updateTrend"
            />
          </AccordinItem>
          <AccordinItem
            :textButton="'Settings'"
            :classIcon="'fa-solid fa-screwdriver-wrench'"
          >
            <BtnGroup
              :checkedLabel="'light mode'"
              :labelTwo="'Dark mode'"
              :classIconCheckedLabel="'fa-solid fa-sun'"
              :classIconlabelTwo="'fa-solid fa-moon'"
              @sendType="addingModeInStore"
              style="border-bottom: solid white 1px;border-radius: 0%;"
            />
            <GroupItemVue :item="'SettingUser'"><i class="ms-2 fa-solid fa-user-gear fs-6"></i></GroupItemVue>
          </AccordinItem>
        </div>
      </div>
      <div class="bor mt-3 mb-2"></div>

      <div
        class="p-1 pt-2 w-75 m-auto text-center d-flex align-items-center justify-content-center"
      >
        <button
          @click="page++"
          class="next loading rounded p-1 m-auto text-white"
          type="button"
          :disabled="!listImageStore"
        >
          <span
            v-if="!listImageStore"
            class="spinner-border spinner-border-sm"
            aria-hidden="true"
          ></span>
          <span style="font-size: 7px" v-else
            ><i class="fa-solid fs-3 fa-circle-right"></i
          ></span>
        </button>
        <button
          @click="reduceNumberPage"
          class="previous loading rounded p-1 m-auto text-white"
          type="button"
          :disabled="!listImageStore"
        >
          <span
            v-if="!listImageStore"
            class="spinner-border spinner-border-sm"
            aria-hidden="true"
          ></span>
          <span style="font-size: 7px" v-else>
            <i class="fa-solid fs-3 fa-circle-left"></i
          ></span>
        </button>
      </div>
    </ManualDiv>
    <div
      style="position: relative; transition: 0.3s"
      class="all"
      :class="{ containerA: !showManual, containerB: showManual }"
    >
      <div
        class="titleSharech text-white  mx-auto mt-2 rounded p-3 d-flex align-items-center"
      >
        <h3 class="textSherch">Showing search results for</h3>
        <span class="sharch ps-2 mb-1">"{{ trend }}"</span>
      </div>
      <div
        v-if="!showVideos && !listFavoritImages"
        class="row row-cols-1 row-cols-md-3 d-flex justify-content-center g-4 pt-5 p-0 mx-1"
      >
        <ProdactCardVue
          v-for="(detailsImg, index) in listImageStore"
          :detailsImg="detailsImg"
          :key="index"
          @favoritIdSend="listFavorit"
        />
      </div>
      <div
        class="row row-cols-1 row-cols-md-3 d-flex justify-content-center g-4 pt-5 p-0"
        v-if="showVideos && !listFavoritImages"
      >
        <VideosCard
          v-for="(details, index) in listVideosStore"
          :key="index"
          :urlVideo="details"
        />
      </div>
      <div
        v-if="listFavoritImages"
        class="row row-cols-1 row-cols-md-3 d-flex justify-content-center g-4 pt-5 p-0"
      >
        <ProdactCardVue
          v-for="(detailsImg, index) in listFavoritImages"
          :detailsImg="detailsImg"
          :key="index"
          :favorit="true"
          @favoritIdSend="listFavorit"
        />
      </div>
      <div
      style="font-size: 100px;color: rgb(71, 71, 255);height: 80vh;"
        class="w-100 d-flex align-items-center justify-content-center"
        v-if="$store.state.listFavoritId.length == 0 && listFavoritImages"
      >
        <i class="fa-regular fa-face-grin-beam-sweat me-1"></i>
        Empty
      </div>
      <div
        v-if="listLength == 0"
        class="limit w-100 text-center fs-3 d-flex align-items-center"
        style="height: 60vh"
      >
        <span
          class="p-3 m-auto rounded d-flex justify-content-center align-items-center"
          style="background-color: rgba(197, 97, 97, 0.716); width: 70%"
          ><i
            class="fa-solid fa-exclamation fa-bounce me-3 fs-2"
            style="color: #c80000"
          ></i
          >You have reached your limit</span
        >
      </div>
      <LodingSpinnerVue v-if="!listImageStore" />
      <FooterMainVue />
    </div>
    <ModalProduct />
  </div>
</template>

<script>
import apiImages from "@/api/api.js";
import apiVideos from "@/api/apiVideos";
import NavbarMain from "@/components/navbarMain/NavbarMain.vue";
import ManualDiv from "@/components/manual/ManualDiv.vue";
import ProdactCardVue from "@/components/prodact/ProductCard.vue";
import MerchantType from "@/components/manual/MerchantType.vue";
import InputChechboxVue from "@/components/manual/InputChechbox.vue";
import AccordinItem from "@/components/manual/AccordinItem.vue";
import VideosCard from "@/components/videosComponents/VideoCard.vue";
import ListGroup from "@/components/prodact/ListGroup.vue";
import BtnGroup from "@/components/loginComponents/BtnGroup.vue";
import LodingSpinnerVue from "@/components/LodingSpinner.vue";
import ModalProduct from "@/components/prodact/ModalProduct.vue";
import FooterMainVue from "@/components/footer/FooterMain.vue";
import GroupItemVue from '@/components/prodact/GroupItem.vue';
export default {
  name: "HomeView",
  components: {
    NavbarMain,
    ManualDiv,
    ProdactCardVue,
    MerchantType,
    InputChechboxVue,
    AccordinItem,
    VideosCard,
    ListGroup,
    BtnGroup,
    LodingSpinnerVue,
    ModalProduct,
    FooterMainVue,
    GroupItemVue
  },
  data() {
    return {
      showManual: false,
      showVideos: false,
      trend: "",
      listFavoritImages: null,
      detailsImg: null,
      page: 1,
      listLength: 1,
    };
  },
  methods: {

    addingModeInStore(mode) {
      this.$store.state.modeSite = mode;
    },

    showManualBoolean(bolean) {
      this.showManual = bolean;
    },

    reduceNumberPage() {
      this.page = Math.max(--this.page, 1);
    },

    checkData(list) {
      this.listLength = list.length;
    },

    checkVideos(boolean) {
      this.showVideos = boolean;
    },
    
    displayType(action) {
      action.classList.toggle("yangVideos");
      const showVideos = action.classList.toString().includes("yangVideos");
      this.showVideos = showVideos;
      showVideos && this.listVideosApi({ trend: this.trend, page: this.page });
    },

    async listImagesApi({ trend, page = this.page }) {
      this.$store.state.listImages = null;
      const list = await apiImages.getListImages({
        trend: trend == "" ? "products camera" : trend,
        page: page,
      });
      this.$store.state.listImages = list.hits;
      this.checkData(list.hits);
    },
    
    async listVideosApi({ trend: trend, page }) {
      const list = await apiVideos.getListVideos({ trend: trend, page: page });
      this.$store.state.listVideos = list.hits;
    },
    searchValue(value) {
      this.trend = value;
      this.page = 1;
    },
    listFavorit(favorit) {
      if (!favorit.remove) {
        this.$store.state.listFavoritId.push(favorit.product);
      } else {
        this.$store.state.listFavoritId =
          this.$store.state.listFavoritId.filter(
            (product) => product != favorit.product
          );
      }
    },
    scrollUpdate() {
      scroll({
        top:0,
        behavior: "smooth",
      });
    },
    checkboxFavorite(checkbox) {
      if (checkbox) {
        this.listFavoritImages = this.listFavoritId;
      } else {
        this.listFavoritImages = null;
      }
    },
    updateTrend(trend) {
      this.trend = trend;
      this.page = 1;
    },
  },
  watch: {
    trend: {
      immediate: true,
      handler() {
        if (!this.showVideos) {
          this.listImagesApi({ trend: this.trend });
        } else this.listVideosApi({ trend: this.trend });
        this.scrollUpdate();
      },
    },
    page: {
      handler() {
        if (!this.showVideos) {
          this.listImagesApi({ trend: this.trend, page: this.page });
        } else this.listVideosApi({ trend: this.trend, page: this.page });
        this.scrollUpdate();
      },
    },
    
  },
  computed: {
    listImageStore() {
      return this.$store.state.listImages;
    },
    listFavoritId() {
      return this.$store.state.listFavoritId;
    },
    listVideosStore() {
      return this.$store.state.listVideos;
    },
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
  mounted() {
    history.pushState(null, null, location.href);
    window.onpopstate = () => {
      history.go(1);
    };
  },
};
</script>

<style scoped>
.all {
  margin-left: auto;
  width: calc(100% - 250px);
  overflow: hidden;
}
.modeDark {
  background: rgba(0, 0, 0, 0.829);
  color: white;
}
::-webkit-scrollbar {
  width: 3px;
}
.loading {
  width: 30%;
  position: relative;
  border: none;
  letter-spacing: 1px;
  background-color: rgba(0, 47, 97, 0.419);
  font-style: italic;
  font-weight: bold;
}
.loading:hover {
  background-color: rgba(6, 8, 86, 0.374);
}
.previous::after {
  content: "Previous";
  position: absolute;
  font-size: 7px;
  left: 10px;
  top: -7px;
  color: rgb(255, 255, 255);
}
.fa-yin-yang {
  transition: 0.5s;
  border-radius: 50%;
  position: absolute;
  width: 14px;
  height: 14px;
  margin-top: 1px;
  margin-left: 1px;
}
.yangVideos {
  translate: 25px 0px;
  transform: rotate(180deg);
}
.ActionButton {
  position: relative;
  width: 40px;
  height: 16px;
  background-color: rgb(255, 255, 255);
  border-radius: 15px;
  box-shadow: 0px 0px 2px 2px rgba(255, 255, 255, 0.504);
}
.next::after {
  content: "Next";
  position: absolute;
  font-size: 7px;
  left: 15px;
  top: -7px;
  color: rgb(255, 255, 255);
}
.showManual {
  transform: translateX(0px);
}
.bor {
  width: 70%;
  margin: auto;
  height: 3px;
  background: rgba(1, 138, 169, 0);
  background: linear-gradient(
    90deg,
    rgba(66, 220, 255, 0) 0%,
    rgba(46, 168, 255, 0.588) 49%,
    rgba(54, 81, 255, 0.46) 100%
  );
  border: none;
  border-radius: 15px;
}
.titleSharech {
  width: 75%;
  background:rgba(87, 105, 241, 0.736)
}
.sharch {
  font-size: 1.3rem;
  font-weight: bold;
}
.textSherch {
  font-size: 1.5rem;
}
@media (max-width: 950px) {
  .containerA {
    width: calc(100% - 40px);
  }
  .containerB {
    width: calc(100% - 250px);
  }
}
@media (max-width: 664px) {
  .titleSharech {
    width: 85%;
  }
  .sharch {
    font-size: 1.1rem;
    font-weight: bold;
  }
  .textSherch {
    font-size: 1.3rem;
  }
}
@media (max-width: 564px) {
  .titleSharech {
    width: 90%;
  }
  .sharch {
    font-size: 1rem;
    font-weight: bold;
  }
  .textSherch {
    font-size: 1.1rem;
  }
  .containerA {
    width: calc(100% - 40px);
  }
  .containerB {
    width: calc(100% - 0px);
  }
}
</style>
