<template>
  <li class="list-group-item p-0 mt-1" @click="sendTrend">
    <button class="btn m-0 p-0 w-100">{{ item }}<slot></slot></button>
  </li>
</template>

<script>
export default {
  name: "groupItem",
  props: {
    item: String,
  },
  methods: {
    sendTrend() {
      this.$emit("trend", this.item);
    },
  },
};
</script>

<style scoped>
.list-group-item {
  background: fixed;
  border: none;
}
.btn {
  color: rgb(255, 255, 255);
  text-align: start;
}

.btn:hover {
  color: #acc5ff;
}
</style>
