<template>
  <div
    class="manual z-3 vh-100 position-fixed text-white"
    :class="{
      showManual: showManual,
      n_showManual: !showManual,
      manualDark: mode,
    }"
    style=""
  >
    <button @click="clickShowManual" style="border: none" class="btnManual p-1">
      <i
        :class="{ rotation: showManual, n_rotation: !showManual }"
        style="transition: 0.5s"
        class="fa-solid fa-forward"
      ></i>
    </button>
    <slot></slot>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showManual: false,
    };
  },
  methods: {
    clickShowManual() {
      this.showManual = !this.showManual;
      this.$emit("showManual", this.showManual);
    },
  },
  computed: {
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
};
</script>

<style scoped>
.manual {
  width: 250px;
  overflow-y: auto;
  transition: 0.5s;
  background-color: rgba(9, 86, 144, 0.897);
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.453);
}
.btnManual {
  color: #fff;
  margin-left: 80%;
  width: 20%;
  background-color: rgba(71, 70, 113, 0.841);
  border-radius: 0%;
  text-align: center;
  display: none;
}
.manualDark {
  background-color: rgb(0, 42, 51);
}
.btnManual:hover {
  background-color: rgba(45, 45, 72, 0.773);
}
.rotation {
  transform: rotate(180deg);
}
.n_rotation {
  transform: rotate(0deg);
  padding-left: 3px;
}
.showManual {
  transform: translateX(0px);
}
@media (max-width: 950px) {
  .n_showManual {
    transform: translateX(-210px);
  }
  .btnManual {
    display: block;
  }
}
</style>
