<template>
  <div
    class="footer mt-4 w-100 pt-5 pb-5 d-flex align-items-center flex-wrap justify-content-evenly"
    style="transition: 0.5s"
    :class="{ footerDark: mode }"
  >
    <ul class="icons d-flex align-items-center">
      <ItemIconSocialMedia
        v-for="(icon, index) in classIcons"
        :key="index"
        :classIcon="icon"
      />
    </ul>
    <div
      class="list d-flex align-items-center justify-content-center flex-wrap align-items-center"
    >
      <ListGroupVue
        style="
          width: 200px;
          padding: 10px;
          margin: auto 10px;
          border-bottom: solid silver 1px;
        "
        :items="classIcons"
      />
      <ListGroupVue
        style="
          width: 200px;
          padding: 10px;
          margin: auto 10px;
          border-bottom: solid silver 1px;
        "
        :items="classIcons"
      />
    </div>
  </div>
</template>

<script>
import ItemIconSocialMedia from "./ItemIconSocialMedia.vue";
import ListGroupVue from "../prodact/ListGroup.vue";
export default {
  name: "footerMain",
  data() {
    return {
      classIcons: [
        "fa-facebook-f",
        "fa-twitter",
        "fa-instagram",
        "fa-pinterest-p",
      ],
    };
  },
  components: {
    ItemIconSocialMedia,
    ListGroupVue,
  },
  computed: {
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
};
</script>

<style scoped>
.icons {
  padding: 0%;
  max-width: 230px;
}
.footer {
  background-color: rgb(0, 104, 127);
}
.footerDark {
  background-color: rgb(0, 42, 51);
}
@media (max-width: 500px) {
  .icons {
    margin: auto;
    margin-top: 18px;
    flex-direction: column;
  }
  .list {
    flex-direction: column;
  }
}
</style>
