<template>
  <div
    class="modal fade overflow-y-auto"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            Product details
          </h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div v-if="detailsProduct" class="modal-body text-center">
          <img
            style="width:100%; height: 300px; object-fit: cover"
            :src="detailsProduct.webformatURL"
            alt=""
          />
          <p style="width: 80%; margin: auto" class="fs-5">
            {{ detailsProduct.tags }}
          </p>
          <ul class="list-group list-group-flush text-start">
            <li class="list-group-item">Price : ${{ detailsProduct.price }}</li>
            <li class="list-group-item">
              Product number : {{ checkNumberProducts }}
            </li>
            <li class="list-group-item d-flex align-items-center">
              Edit product number
              <div>
                <button @click="productNumber++" class="btn fs-4">
                  <i class="fa-solid fa-circle-plus"></i>
                </button>
                <button
                  @click="productNumber > 1 ? productNumber-- : productNumber"
                  class="btn fs-4"
                >
                  <i class="fa-solid fa-square-minus"></i>
                </button>
              </div>
              <div class="form-floating" style="width: 50%">
                <input
                  v-model="productNumber"
                  type="number"
                  class="form-control"
                  id="floatingInput"
                  placeholder="number"
                  :class="{ errorNumberProducts: checkNumberProducts == 0 }"
                />
                <label for="floatingInput"
                  >Number<i
                    v-if="checkNumberProducts == 0"
                    class="fa-solid fa-bug fa-bounce fs-4 ms-1"
                    style="color: #b30f0fb1"
                  ></i
                ></label>
              </div>
            </li>
            <li class="list-group-item fw-bold">
              Total price : ${{ checkNumberProducts * detailsProduct.price }}
            </li>
            <li class="list-group-item fw-bold">
              <a :href="detailsProduct.link" target="_blank"
                >Go to the official website of the product</a
              >
            </li>
          </ul>
        </div>
        <div class="modal-footer d-flex align-items-center">
          <span
            v-if="massage"
            style="
              color: rgb(254, 92, 92);
              background-color: rgb(255, 179, 179);
              font-size: 12px;
            "
            class="p-2 rounded me-2"
            >{{ massage }}</span
          >
          <a
            class="mx-1"
            data-bs-toggle="modal"
            data-bs-target="#exampleModal"
            style="color: blue; cursor: pointer"
            v-if="massage"
            @click="$router.push('/login')"
            >login</a
          >
          <button
            type="button"
            @click="clickHandlerSave"
            class="btn btn-primary"
            :disabled="checkNumberProducts == 0"
          >
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "modalProduct",
  data() {
    return {
      productNumber: 1,
      massage: "",
    };
  },
  computed: {
    detailsProduct() {
      return this.$store.state.listSpecificProducts[
        this.$store.state.indexDetailsProduct
      ];
    },
    userDetails() {
      return this.$store.state.user;
    },
    checkNumberProducts() {
      if (this.productNumber <= 0) {
        return (this.productNumber = 0);
      } else if (this.productNumber <= 10 && this.productNumber > 0)
        return this.productNumber;
      else return (this.productNumber = 10);
    },
  },
  methods: {
    clickHandlerSave() {
      if (this.userDetails.email != "") {
      } else {
        this.showMasseg();
      }
    },
    showMasseg() {
      if (
        this.$store.state.indexLoginUser == null &&
        this.userDetails.email == ""
      )
        this.massage = "Purchases can only be made if you are logged in";
    },
  },
};
</script>

<style scoped>
.errorNumberProducts,
.errorNumberProducts:focus {
  box-shadow: 0 0 0 0.25rem rgba(253, 13, 13, 0.477);
}
</style>
