<template>
  <div
    @mouseenter="playVideo($refs.video)"
    @mouseleave="pauseVideo($refs.video)"
    @click="clickHandler"
    style="width: 300px; position: relative"
    class="col"
  >
    <div
      class="title w-75 position-absolute ps-1 text-white"
      style="bottom: 10%"
    >
      {{ urlVideo.tags }}
      <!-- <button class="btn p-0 px-2">
        <i class="fa-solid fa-star"></i>
      </button> -->
    </div>

    <!--  <img :src="urlVideo.userImageURL" alt=""> -->
    <video
      ref="video"
      style="object-fit: cover"
      class="rounded"
      :src="urlVideo.videos.tiny.url"
      muted
      width="260"
      height="300"
      preload="metadata"
      loop
    >
      <!-- <source :src="urlVideo.videos.tiny.url" type="video/mp4" />
      <source :src="urlVideo.videos.tiny.url" type="video/ogg" /> -->
      Your browser does not support the video tag.
    </video>
    <!-- <p>{{ urlVideo.views }}</p> -->
  </div>
</template>

<script>
export default {
  name: "videosCard",
  props: {
    urlVideo: Object, //TODO;update
  },
  methods: {
    playVideo(video) {
      video.play();
    },
    pauseVideo(video) {
      video.pause();
    },
    clickHandler() {
      this.$store.state.singleVideo = this.urlVideo.id
      this.$router.push('/video');
    }
  },
};
</script>

<style scoped>
.col {
  cursor: pointer;
}
.title {
  display: none;
}
.col:hover .title {
  display: block;
}
