<template>
  <div class="accordion-item " style="background: fixed;border: none;">
    <h2 class="accordion-header">
      <button
        style="background: fixed; font-weight: 600;border: none;"
        class="accordion-button pe-5 collapsed "
        type="button"
        data-bs-toggle="collapse"
        :data-bs-target="`#${dataTarget}`"
        aria-expanded="false"
        :aria-controls="dataTarget"
      >
        {{ textButton }}<i class="px-2" :class="classIcon"></i>
      </button>
    </h2>
    <div
      style="background:  rgba(0, 55, 67, 0.285)"
      :id="dataTarget"
      class="accordion-collapse collapse"
      data-bs-parent="#accordionFlushExample"
    >
      <div class="accordion-body">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "accordionItem",
  props: {
    textButton: String,
    classIcon: String,
  },
  computed: {
    dataTarget() {
      return this.textButton.split(" ").join("");
    },
  },
};
</script>

<style scoped>
button {
  color: rgb(187, 243, 255);
}
.accordion-button:focus {
  box-shadow: none;
  color:rgb(116, 155, 255)
}
