<template>
  <div class="col">
    <div
      class="card rounded overflow-hidden w-100"
      style="height: 100%"
      :class="{ cardDark: mode }"
    >
      <div
        class="w-100 p-1 text-center"
        style="background-color: rgba(0, 0, 0, 0.049)"
      >
        <img
          :src="detailsImg.webformatURL"
          class="card-img-top rounded"
          style="object-fit: cover"
        />
      </div>
      <div
        class="card-body pt-3 p-2 d-flex flex-column justify-content-center overflow-y-auto"
      >
        <div class="mt-2">
          <p class="card-text mb-0">A type of</p>
          <h6 class="card-title">{{ detailsImg.tags }}</h6>
        </div>
        <p
          class="fw-bold mb-1 fs-5 d-flex justify-content-between align-items-center"
          style="color: blue; position: relative; bottom: 0"
        >
          <span v-if="!detailsImg.price">${{ price }}0</span>
          <span v-else>${{ detailsImg.price }}</span>
          <i class="fa-solid fa-star fs-6" style="color: #c4c400"
            >{{ evaluationProdact }}.{{ evaluationProdact }}</i
          >
        </p>
        <div
          class="w-100 mt-auto d-flex justify-content-evenly"
          style="background-color: rgba(0, 73, 77, 0.841)"
        >
          <button
            @click="clickCardHandler"
            :class="{ buying_on: buying }"
            class="buying m-0 btn"
          >
            <i class="fa-solid fa-bag-shopping"></i>{{ price + buyingNumber }}
          </button>
          <button
            @click="clickFavoritHandler($refs.heart, detailsImg)"
            class="heart btn ms-2 px-1"
            :class="{ redHear: favorit }"
            ref="heart"
          >
            <i class="fa-solid fa-heart"></i>
          </button>
          <button
            ref="likes"
            @click="onLikes($refs.likes)"
            class="like btn ms-2"
          >
            <i class="fa-regular fa-thumbs-up"></i>
            {{ numberLikes }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "prodactCard",
  props: {
    detailsImg: {
      webformatURL: String,
      tags: String,
      googleUrl: String,
      price: Number,
    },
    favorit: Boolean,
  },
  data() {
    return {
      buying: false,
      buyingNumber: 0,
      price: Math.round(Math.random() * 135),
      likes: Math.round(Math.random() * 1115),
    };
  },
  methods: {
    clickFavoritHandler(ele, product) {
      ele.classList.toggle("redHear");
      product.price = this.price * 10;
      product.likes = this.likes;
      if (ele.classList.toString().includes("redHear")) {
        this.favoritIdSend({ product: product, remove: false });
      } else this.favoritIdSend({ product: product, remove: true });
    },
    favoritIdSend({ product, remove }) {
      this.$emit("favoritIdSend", { product, remove });
    },
    clickCardHandler() {
      this.detailsImg.price = this.price * 10;
      this.detailsImg.likes = "this.likes";
      this.listSpecificProducts.forEach((element) => {
        if (element != this.detailsImg) {
          this.buying = true;
        } else this.buying = false;
      });
      if (this.buying || this.listSpecificProducts.length == 0) {
        this.listSpecificProducts.push(this.detailsImg);
        this.buying = true;
        this.buyingNumber = 1;
      }
    },
    onLikes(ele) {
      ele.classList.toggle("onLike");
      if (ele.classList.toString().includes("onLike")) {
        this.likes += 1;
      } else this.likes -= 1;
    },
  },
  computed: {
    evaluationProdact() {
      return Math.round(Math.random() * 7.5);
    },
    listSpecificProducts() {
      return this.$store.state.listSpecificProducts;
    },
    numberLikes() {
      if (this.likes < 999 && this.likes >= 100) {
        return this.likes;
      } else if (this.likes > 999) {
        return `${this.myArr[0]}.${this.myArr[1]}Th`;
      } else return this.likes;
    },
    myArr() {
      const myArr = String(this.likes)
        .split("")
        .map((num) => {
          return Number(num);
        });
      return myArr;
    },
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
};
</script>

<style scoped>
.col {
  max-width: 300px;
  max-height: 435px;
}
img {
  height: 246px;
  width: 100%;
  margin: auto;
  border-radius: 0%;
  object-fit: cover;
}
.onLike {
  color: rgb(145, 145, 255);
  font-weight: bold;
}
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px silver;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgb(0, 73, 77);
}
.card {
  border-radius: 0%;
  text-align: start;
  border: none;
  border: solid #8b8b8b5d 1px;
  transition: 0.3s;
}
button{
  min-width: 50px
}
.cardDark {
  border: none;
  background: rgb(28, 28, 28);
  color: white;
}
.card:hover {
  background: #eee;
  translate: 0px -6px;
}
.cardDark:hover {
  background: rgba(33, 33, 33, 0.851);
  translate: 0px -6px;
}
.card-text {
  color: #989898;
  font-size: 14px;
}

.like,
.like:active,
.heart,
.heart:active,
.buying:active,
.buying {
  color: white;
  border: none;
  background: fixed;
  border-radius: 0%;
  margin: 0px 1px;
}
.buying {
  color: rgb(255, 113, 113);
}
.buying_on {
  color: rgb(118, 118, 254);
}
.redHear {
  color: rgb(255, 85, 85);
}

.heart:hover,
.buying:hover,
.like:hover {
  background: #22222291;
}
</style>
